body {
  color: white;
  background-color: #004C3F;
  margin: 0;
  font-family: adelle-sans, helvetica, serif;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: adelle-sans, sans-serif;
  font-style: normal;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

.App {
  overflow-y: hidden;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  height: 100vh;
}

h3 {
  font-weight: 100;
}

a {
  display: inline-block;
  position: relative;
  text-decoration: none;
  color: #ED5B47;
}


main {
  padding: 10px;
  height: 90vh;
  width: 75vh;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

html {
  box-sizing: border-box;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #004C3F;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.AIChatBubble,
.HumanChatBubble {
  display: flex;
  font-size: 15px;
  font-weight: 100;
  line-height: 25px;
  padding: 0px 20px;
  color: black;
  width: 70%;
  min-height: 75px;
  overflow: hidden;
  box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.10), 0 0 5px 0 rgba(0, 0, 0, 0.20), inset 0 0 2px 0 #FFFFFF;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  margin: 20px;
  align-items: center;
  -ms-word-break: break-all;
  /* This is the dangerous one in WebKit, as it breaks things wherever */
  word-break: break-all;
  /* Instead use this non-standard one: */
  word-break: break-word;

  /* Adds a hyphen where the word breaks, if supported (No Blink) */
}

.AIChatBubble {
  background: #FFFFFF;
  border-radius: 16px 16px 16px 0px;
  display: flex;
  align-items: center;
  margin-left: 0;
  margin-right: auto;
  width: 100%;
}

.HumanChatBubble {
  border-radius: 16px 16px 0px 16px;
  background: #e9f2d9;
  margin-left: auto;
  margin-right: 0;
}

.delay-2 {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}

.delay-4 {
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}

.fadeInBottom {
  -webkit-animation-name: fadeInBottom;
          animation-name: fadeInBottom
}

main::-webkit-scrollbar {
  width: 0.25rem;
  height: 0.25rem;
}

main::-webkit-scrollbar-track {
  margin-top: 2vh;
  margin-bottom: 7vh;
}

main::-webkit-scrollbar-thumb {
  background: black;
}

main:active::-webkit-scrollbar-thumb {
  visibility: visible;
}

@-webkit-keyframes fadeInBottom {
  from {
    opacity: 0;
    -webkit-transform: translateY(20%);
            transform: translateY(20%);
  }

  to {
    opacity: 1
  }
}

@keyframes fadeInBottom {
  from {
    opacity: 0;
    -webkit-transform: translateY(20%);
            transform: translateY(20%);
  }

  to {
    opacity: 1
  }
}
.Header h1 {
    margin: 0;
}

.Header h2 {
    margin-top: 4px;
}

#hello {
    color: #0f7b65;
    font-weight: 600;
    font-style: bold;
}

#warning {
    color: #0f7b65;
    font-weight: 400;
    font-style: italic;
}

.Header img {
    width: 15px;
    display: inline-block;
}
form {
    position: fixed;
    bottom: 0;
    background-color: white;
    max-width: 728px;
    display: flex;
    font-size: 1.5rem;
    width: 100%;
    text-align: left;
    color: #292a2c;
    box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.1), 0 0 5px 0 rgba(0, 0, 0, 0.2),
        inset 0 0 2px 0 #ffffff;
    padding: 7px;
    border-radius: 4px;
    margin-bottom: 5vh;
}

#emoji {
    font-style: normal;
}

textarea {
    -webkit-appearance: none;
    font-family: adelle-sans, helvetica, serif;
    resize: none;
    width: 80%;
    /* Safari/Chrome, other WebKit */
    /* Firefox, other Gecko */
    box-sizing: border-box;
    outline: none;
    text-transform: none;
    display: block;
    font-size: 18px;
    outline: 0;
    overflow: auto;
    border: white 2px solid;
    min-height: 50px !important;
    overflow: hidden;
}

::-webkit-input-placeholder {
    max-height: 100%;
    line-height: 50px;
}

:-ms-input-placeholder {
    line-height: 50px;
}

textarea::-webkit-input-placeholder {
    line-height: 50px;
}

textarea:-ms-input-placeholder {
    line-height: 50px;
}

textarea::placeholder {
    line-height: 50px;
}

textarea:focus::-webkit-input-placeholder {
    color: transparent;
}

textarea:focus:-ms-input-placeholder {
    color: transparent;
}

textarea:focus::placeholder {
    color: transparent;
}


.Submit {
    display: block;
    margin-left: auto;
    margin-right: 0;
    padding: 1rem 2rem;
    text-decoration: none;
    color: white;
    cursor: pointer;
    background-color: white;
    text-align: center;
    transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
    transition: background 250ms ease-in-out, transform 150ms ease;
    transition: background 250ms ease-in-out, transform 150ms ease, -webkit-transform 150ms ease;
    -webkit-appearance: none;
    -moz-appearance: none;
    border: 0;
    display: flex;
    align-items: center;
}

.Submit svg {
    text-align: right;
    width: 27px;
    fill: #0f7b65;
    transition: 0.5s;
}

.Submit svg:hover,
.Submit svg:focus,
.Submit svg:active {
    fill: #ED5B47;
}

.Submit svg:active {
    fill: #AF4237;
    -webkit-transform: scale(0.87);
            transform: scale(0.87);
}
.Response {
    margin: 1vh;
}

.empty {
    height: 10px;
}
.spinner-container {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    padding: 2rem 0;
    margin: 0 -5%;
    overflow: hidden;
}

.lineUp {
    -webkit-animation: 2s anim-lineUp ease-out;
            animation: 2s anim-lineUp ease-out;
}

@-webkit-keyframes anim-lineUp {
    0% {
        opacity: 0;
        -webkit-transform: translateY(80%);
                transform: translateY(80%);
    }

    20% {
        opacity: 0;
    }

    50% {
        opacity: 1;
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}

@keyframes anim-lineUp {
    0% {
        opacity: 0;
        -webkit-transform: translateY(80%);
                transform: translateY(80%);
    }

    20% {
        opacity: 0;
    }

    50% {
        opacity: 1;
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}

.dot-falling {
    justify-content: center;
    position: relative;
    left: -9999px;
    display: flex;
    align-items: center;
}

.dot-falling::before,
.dot-falling::after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
}

.dot-falling::before {
    width: 20px;
    height: 6px;
    border-radius: 5px;
    background-color: white;
    color: white;
    -webkit-animation: dotFallingBefore 1s infinite linear;
            animation: dotFallingBefore 1s infinite linear;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

.dot-falling::after {
    width: 20px;
    height: 6px;
    border-radius: 5px;
    background-color: red;
    color: red;
    -webkit-animation: dotFallingAfter 1s infinite linear;
            animation: dotFallingAfter 1s infinite linear;
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
}

@-webkit-keyframes dotFallingBefore {
    0% {
        box-shadow: 9984px -15px 0 0 rgba(152, 128, 255, 0);
    }

    25%,
    50%,
    75% {
        box-shadow: 9984px 0 0 0 white;
    }

    100% {
        box-shadow: 9984px 15px 0 0 rgba(152, 128, 255, 0);
    }
}

@keyframes dotFallingBefore {
    0% {
        box-shadow: 9984px -15px 0 0 rgba(152, 128, 255, 0);
    }

    25%,
    50%,
    75% {
        box-shadow: 9984px 0 0 0 white;
    }

    100% {
        box-shadow: 9984px 15px 0 0 rgba(152, 128, 255, 0);
    }
}

@-webkit-keyframes dotFallingAfter {
    0% {
        box-shadow: 10014px -15px 0 0 rgba(152, 128, 255, 0);
    }

    25%,
    50%,
    75% {
        box-shadow: 10014px 0 0 0 #96bf48;
    }

    100% {
        box-shadow: 10014px 15px 0 0 rgba(152, 128, 255, 0);
    }
}

@keyframes dotFallingAfter {
    0% {
        box-shadow: 10014px -15px 0 0 rgba(152, 128, 255, 0);
    }

    25%,
    50%,
    75% {
        box-shadow: 10014px 0 0 0 #96bf48;
    }

    100% {
        box-shadow: 10014px 15px 0 0 rgba(152, 128, 255, 0);
    }
}
